import StockLibrary from "../Classes/StockLibrary";
import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import SearchItems from "./SearchItems";
import SearchBox from "./SearchBox";
import "./Search.css";
import debounce from "lodash/debounce";
//import ErrorBoundary from "./ErrorBoundary";

import { setSearchValue, requestSearchedStocks } from "../actions";

const mapDispatchToProps = (dispatch) => {
  return {
    onSearchChange: (event) => dispatch(setSearchValue(event.target.value)),
    onRequestStocks: (search, searchValue) =>
      dispatch(requestSearchedStocks(search, searchValue)),
  };
};

const mapStateToProps = (state) => {
  return {
    searchValue: state.search.searchValue,
    stocks: state.requestSearchedStocks.stocks,
    isPending: state.requestSearchedStocks.isPending,
    error: state.requestSearchedStocks.error,
  };
};

const Search = ({
  searchValue,
  onSearchChange,
  onRequestStocks,
  stocks,
  isPending,
  error,
  height,
}) => {
  const debounce_verify = debounce(() => verifySearch(), 5000);

  const verifySearch = () => {
    let regex = /[` !@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/;

    if (
      !regex.test(searchValue) &&
      searchValue !== "" &&
      searchValue.length < 10 &&
      searchValue !== undefined
    ) {
      onRequestStocks(true, searchValue);
    } else if (searchValue.includes(" ")) {
    } else {
      onRequestStocks(false, searchValue);
    }
  };
  console.log("reloaded");
  useEffect(() => {
    debounce_verify();
  }, [searchValue]);

  return (
    <div className="search">
      <SearchBox search={onSearchChange} />
      <SearchItems items={stocks} height={height} />
    </div>
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(Search);
