import React, { useState, useEffect } from "react";
import "./App.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import WatchlistPage from "./WebPages/WatchlistPage.js";
import MarketPage from "./WebPages/MarketPage";
import AccountPage from "./WebPages/Account/AccountPage";
import StocksPage from "./WebPages/StocksPage";
import RoboTradingPage from "./WebPages/RoboTradingPage";
import TradingPage from "./WebPages/TradingPage";
import NoMatch from "./WebPages/NoMatch";
import PageContainer from "./WebPages/PageContainer";
import socketIOClient from "socket.io-client";
import io from "socket.io-client";
import { setSearchValue } from "./actions";

const ENDPOINT = "http://localhost:8080";
function App() {
  const [response, setResponse] = useState(0);

  console.log("app reloaded");
  useEffect(() => {
    let socket = io.connect();

    socket.on("FromAPI", (data) => {
      console.log(data);
      setResponse(data);
    });
    return () => socket.disconnect();
  }, []);

  return (
    <div className="App">
      <div>{response}</div>
      <PageContainer>
        <Router>
          <Switch>
            <Route exact path="/app">
              <MarketPage />
            </Route>
            <Route path="/app/watchlist">
              <WatchlistPage />{" "}
            </Route>
            <Route path="/app/account">
              <AccountPage />{" "}
            </Route>
            <Route path="/app/stocks">
              <StocksPage />{" "}
            </Route>
            <Route path="/app/trade">
              <TradingPage />
            </Route>
            <Route path="/app/robo-trade">
              {" "}
              <RoboTradingPage />
            </Route>
            <Route path="*">
              <NoMatch />
            </Route>
          </Switch>
        </Router>
      </PageContainer>
    </div>
  );
}

export default App;
