import {
  CHANGE_SEARCH_VALUE,
  SEARCH_STOCKS_PENDING,
  SEARCH_STOCKS_SUCCESS,
  SEARCH_STOCKS_FAILED,
  CHANGE_SEARCHED_STOCKS,
} from "./constants.js";

export const setSearchValue = (text) => ({
  type: CHANGE_SEARCH_VALUE,
  payload: text,
});

export const requestSearchedStocks = (search, searchValue) => (dispatch) => {
  if (search) {
    dispatch({ type: SEARCH_STOCKS_PENDING });
    let url = `https://www.alphavantage.co/query?function=SYMBOL_SEARCH&keywords=${searchValue}&apikey=CVH0XQG208A1LHZ6`;
    fetch(url)
      .then((resp) => resp.json())
      .then((resp) =>
        dispatch({ type: SEARCH_STOCKS_SUCCESS, payload: resp["bestMatches"] })
      )
      .catch((error) =>
        dispatch({ type: SEARCH_STOCKS_FAILED, payload: error })
      );
  } else {
    dispatch({ type: SEARCH_STOCKS_SUCCESS, payload: [] });
  }
};
